import { Component, OnInit } from '@angular/core';
import { LunguageService } from '../lunguage.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit{
heb:boolean=true;
eng:boolean=false;
rus:boolean=false;
  constructor(private serviceLung:LunguageService) { 
    

  }

 

  ngOnInit(): void {
    this.serviceLung.sharedHeb.subscribe(heb => this.heb=heb);
    this.serviceLung.sharedEng.subscribe(eng => this.eng=eng);
    this.serviceLung.sharedRus.subscribe(rus => this.rus=rus);



  }

}
