import { Component, OnInit } from '@angular/core';
import { LunguageService } from '../lunguage.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  public eng:boolean=false;
  public heb:boolean=false;
  public rus:boolean=false;
  public menuToggle:boolean=true;

  constructor(private serviceLung:LunguageService) { }


  ngOnInit(): void {
    this.serviceLung.sharedHeb.subscribe(heb => this.heb=heb);
    this.serviceLung.sharedEng.subscribe(eng => this.eng=eng);
    if(document.documentElement.clientWidth<736){
      this.menuToggle=false;
    }
 

  }
russiaClick(){
}
hebClick(){
  this.heb=true;
  this.eng=false;
  this.rus=false;
  this.serviceLung.nextHeb(this.heb);
  this.serviceLung.nextEng(this.eng);
  this.serviceLung.nextRus(this.rus);



  }
  engClick(){
    this.eng=true;
    this.heb=false;  
    this.rus=false;
    this.serviceLung.nextHeb(this.heb);
    this.serviceLung.nextEng(this.eng);
    this.serviceLung.nextRus(this.rus);

  }

  rusClick(){
    this.eng=false;
    this.heb=false;  
    this.rus=true;
    this.serviceLung.nextHeb(this.heb);
    this.serviceLung.nextEng(this.eng);
    this.serviceLung.nextRus(this.rus);

  }
  toggle(){
    this.menuToggle=!this.menuToggle;
  }
}