import { Component, OnInit } from '@angular/core';
import { LunguageService } from '../lunguage.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
  public heb:boolean=true;
  public eng:boolean=false;
  public rus:boolean=false;

  constructor(private serviceLung:LunguageService) { }

  ngOnInit(): void {
    this.serviceLung.sharedHeb.subscribe(heb => this.heb=heb);
    this.serviceLung.sharedEng.subscribe(eng => this.eng=eng);
    this.serviceLung.sharedRus.subscribe(rus => this.rus=rus);

  }

}
