import { Component, OnInit } from '@angular/core';
import { LunguageService } from '../lunguage.service';

@Component({
  selector: 'app-contcat',
  templateUrl: './contcat.component.html',
  styleUrls: ['./contcat.component.css']
})
export class ContcatComponent implements OnInit {
  public heb:boolean=true;
  public eng:boolean=false;
  public rus:boolean=false;


  constructor(private serviceLung:LunguageService) { }

  ngOnInit(): void {
    this.serviceLung.sharedHeb.subscribe(heb => this.heb=heb);
    this.serviceLung.sharedEng.subscribe(eng => this.eng=eng);
    this.serviceLung.sharedRus.subscribe(rus => this.rus=rus);

  }

  clickIntegram(){
    window.location.href='https://instagram.com/staseamakeup?utm_medium=copy_link';
  }
  
  clickFacebook(){
    window.location.href='https://www.facebook.com/stasea.panov';
  }
  whatsapp(){
    window.location.href="https://api.whatsapp.com/send?phone=+972542285211";
  }

}
