import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { of } from 'rxjs/internal/observable/of';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})
export class LunguageService {


  public heb = new BehaviorSubject(true);
  sharedHeb = this.heb.asObservable();

  public eng = new BehaviorSubject(false);
  sharedEng = this.eng.asObservable();

  public rus = new BehaviorSubject(false);
  sharedRus = this.rus.asObservable();

  constructor() { }
  nextHeb(heb: boolean) {
    this.heb.next(heb)
  }

  nextEng(eng: boolean) {
    this.eng.next(eng)
  }

  nextRus(rus: boolean) {
    this.rus.next(rus)
  }


}
